}
.stories-content h5 {
	font-size: 16px;
	color: #a8a8a8;
	padding-bottom: 15px;
}
.stories-content p {
	font-size: 15px;
	line-height: 22px;
	color: #565656;
	padding-bottom: 15px;
}
.stories-content p i {
	font-size: 22px;
	color: #cecece;
	padding-right: 8px;
}
/*=================================
-------------- stats --------------
=================================*/
#stats {
	background: whitesmoke;
